import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import Item from "./Item"
import { SearchIcon, Trash } from "lucide-react"
import { Input } from "@/components/ui/input"
import { useState } from "react"


const TrashCan = () => {
    const [search, setSearch] = useState("");
    

  return (
    <Popover>
        <PopoverTrigger className="w-full mt-4">
            <Item label="Trash"icon={Trash}/>
            <PopoverContent className="p-0 w-72" side = 'right'/* {isMobile?"bottom":"right"} */ >
                <div className="space-x-1 p-2 flex items-center">
                    <SearchIcon className="h-4 flex justify-center w-4"/>
                    <Input 
                        value={search}
                        onChange={(e) => setSearch(e.target.value)} 
                        className="h-7 px-2 focus-visible:ring-transparent bg-secondary"
                        placeholder="Filter by page title"/>
                </div>
                <div className="mt-2 px-1 pb-1">
                    <p className="hidden last:block text-sm text-center text-muted-foreground">
                        No notes available..
                    </p>

                </div>
            </PopoverContent>
        </PopoverTrigger>
    </Popover>  
  )
}

export default 
TrashCan